
import {formatDate} from 'common/js/date.js';
import Vue from 'vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(AlertPlugin)
export default {
  name: 'comments',
  props: {
    title:String,
    commentlist:Array
  },
  components: {
  },
  filters: {
    formatDate:function (time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd");
    }
  },
  methods: {
    showPlugin () {
      this.$vux.alert.show({
        title: '小提示',
        content: '打开APP，参与互动，<br/> 获取优质内容。',
        dialogTransition:"",
        maskTransition:"",
        onShow () {
          console.log('Plugin: I\'m showing')
        },
        onHide () {
          console.log('Plugin: I\'m hiding now')
        }
      })
    },
    showPluginAuto () {
      this.showPlugin()
      // setTimeout(() => {
      //   this.$vux.alert.hide()
      // }, 3000)
    }
  }
}



