
import 'common/css/reset.css';
import 'common/js/common.js';
import geturlpara from 'common/js/geturlpara.js';
import {formatDate} from 'common/js/date.js';
import Comment from "components/Comment/Comment"
import Audiobox from "components/Audio/Audio"
import Videobox from "components/Video/Video"
import Vue from 'vue'
import Loading from 'vux/src/components/loading/index.vue'

import VueResource from 'vue-resource'
Vue.use(VueResource)
Vue.prototype.$geturlpara=geturlpara

//sub_type:a:文章 r：音频  v：视频
export default {
  name: 'detail',
  data () {
    return {
      showContent:false,
      paly:0,
      showplay:true,
      title:"评论",
      commentlist:{
        "items":[]
      },
      articles:{},
      loadingshow: true,
      loadtext: 'loading...'
    }
  },
  components: {
    Loading,
    Comment,
    Audiobox,
    Videobox
  },
  created () {
    var id = this.$geturlpara.getUrlKey("id")||"1482";
    this.fetchData(id);
  },
  methods: {
    openApp(){
      console.log("open");
      console.log(HOST);
      this.getPhoneType();
    },
    //获取数据
    fetchData(id){
      this.$http.get(HOST+'/api/articles/'+id+'.json', [])
      .then((data)=>{
        this.loadingshow=false;
        this.articles=JSON.parse(data.bodyText);
        this.fetchCommentData(id);
        if(this.articles.status!=0){
          //返回为4，无权限
          if(this.articles.status==4){
            window.location.href="https://kofuf.com:8443/privilege/h5_article/app_download.html"
          }
          this.logErr(this.articles.error);
        }else{
          this.showContent=true;
        }
      }, (err)=>{
        this.loadingshow=false;
        this.logErr("请在网络环境下访问");
        console.log(err);
      });
      //10秒之后loading消失
      var self=this;
      setTimeout(()=>{
        self.loadingshow=false;
      },10000);
    },

    //请求评论数据
    fetchCommentData(id){
      this.$http.get(HOST+'/api/articles/comments.json?id='+id, [])
      .then((data)=>{
        this.loadingshow=false;
        this.commentlist.items=JSON.parse(data.bodyText).items;
      }, (err)=>{
        this.loadingshow=false;
        console.log(err);
      });
    },
    logErr(err){
      this.$vux.alert.show({
        title: '提示',
        content: err,
        dialogTransition:"",
        maskTransition:"",
        onShow () {},
        onHide () {}
      })
    },
    //判断手机类型
    getPhoneType(){
      var u = navigator.userAgent;
      //应用市场地址：
      if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {//安卓手机
          console.log("Android");
          window.location.href="http://a.app.qq.com/o/simple.jsp?pkgname=com.avatar.kungfufinance"
        } else if (u.indexOf('iPhone') > -1) {//苹果手机
          console.log("apple");
          this.logErr('apple');
        } else if (u.indexOf('Windows Phone') > -1) {//winphone手机
          console.log("Windows");
          window.location.href="http://a.app.qq.com/o/simple.jsp?pkgname=com.avatar.kungfufinance"
        }
      }
  },
  filters: {
    formatDate:function (time) {
      var date = new Date(time);
      return formatDate(date, "MM月dd日");
    }
  }
}



