export default {
  name: 'vediobox',
  props: {
    video:String,
    poster:String
  },
  data () {
    return {
      showplay:true,
    }
  },
  components: {

  },
  methods:{
    play(){
      if(this.$refs.vdo.paused){
        this.$refs.vdo.play();
        this.showplay=false;
      }else{
        this.$refs.vdo.pause();
        this.showplay=true;
      }
    }
  }
}



